
  import { Component, OnInit, ViewEncapsulation } from '@angular/core';
  // import { LookupService, AssetService } from 'src/app/api/services';
  import { CookieService } from 'ngx-cookie-service';
  import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-operational-maintanance-list',
  templateUrl: './operational-maintanance-list.component.html',
  styleUrls: ['./operational-maintanance-list.component.scss']
})
export class OperationalMaintananceListComponent implements OnInit {


	public subCategoryList: any = [];
	public assetList: any = [];
	public assetId: any;
	public search: any = {};
	public maintList:any = [{},{}];

	constructor(
		private route: ActivatedRoute,
		private cookieService: CookieService,
	) { }


	ngOnInit(): void {
		this.route.paramMap.subscribe(params => {
			if (params) {
				this.assetId = params.get('id');
			}
		})
	
		
	}

	
}
