import { Component, Injector, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { ModalService } from '../../../shared/services/modal.service';
import { SharedService } from '../../../shared/services/shared.service';

@Component({
	selector: 'app-operational-maintanance-setup',
	templateUrl: './operational-maintanance-setup.component.html',
	styleUrls: ['./operational-maintanance-setup.component.scss']
})
export class OperationalMaintananceSetupComponent implements OnInit {

	public categoriesData: any = [];
	public modalService: any;
	public selectedTab: string;
	public row: any = {};
	public isAssetLoaded: boolean;
	public opIndex: number;
	public ItemStartIndex: any = 0;
	public ItemEndIndex: any;
	public totalItems: any;
	public itemLimit: any = 10;
	public unitFieldType: string = "unitno";
	public unitOrder: boolean = false;
	public subItemStartIndex: any = 0;
	public subItemEndIndex: any;
	public subTotalItems: any;
	public subItemLimit: any = 10;
	public subCategoriesData: any = [];
	public tempSubCategoriesData: any = [];
	public subCatgory: any = {};
	opSubIndex: number;
	deleteSubCatgory: boolean;

	constructor(
		private cookieService: CookieService,
		private injector: Injector,
		private sharedService: SharedService,
		private genMaintService: GenMaintService,
	) {
		this.modalService = this.injector.get(ModalService);
	}

	ngOnInit(): void {
		this.getAllCategory();
		this.getAllSubCategory();

		this.selectedTab = 'category';  //Default selected tab	
		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
				let param: any = {};
				if (this.deleteSubCatgory == true) {
					param = {
						genMaintenanceSubCategoryId: id,
						deleteBy: parseInt(this.cookieService.get('userId'))
					}
					this.genMaintService.deleteGenMaintenanceSubCategory(param).subscribe((res: any) => {
						setTimeout(() => {
							this.sharedService.setAlertMessage(" Sub Category deleted successfully");
							this.sharedService.setUnitListDeleteIndex(null);
							this.getAllSubCategory();
							this.deleteSubCatgory = false;
						}, 500)
					}, error => {
						console.log(error);
					});
				} else {
					param = {
						genMaintenanceCategoryId: id,
						deleteBy: parseInt(this.cookieService.get('userId'))
					}
					this.genMaintService.deleteGenMaintenanceCategory(param).subscribe((res: any) => {
						setTimeout(() => {
							this.sharedService.setAlertMessage("Category deleted successfully");
							this.sharedService.setUnitListDeleteIndex(null);
							this.getAllCategory();
						}, 500)
					}, error => {
						console.log(error);
					});
				}
			}


		});
	}

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}
	getIndexParamsSubcategory(event) {
		this.subItemStartIndex = event.subItemStartIndex;
		this.subItemEndIndex = event.subItemEndIndex;
		this.subItemLimit = event.subItemLimit;
	}


	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}
	// Tab
	showTab(type) {
		this.selectedTab = type;
	}
	// Tab
	isActive(type) {
		return this.selectedTab == type ? true : false;
	}



	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'desc' : 'asc';
		}
		else return '';
	}

	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}


	deleteRow(index: number, data) {
		this.modalService.showConfirmModal(data.genMaintenanceCategoryId);
	}
	deleteSubRow(index: number, data) {
		this.deleteSubCatgory = true;
		this.modalService.showConfirmModal(data.genMaintenanceSubCategoryId);
	}




	getAllCategory() {
		this.isAssetLoaded = true;
		this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
			if (res) {
				this.categoriesData = res ? res : [];
				this.totalItems = this.categoriesData.length;
				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				}
				else {
					this.ItemEndIndex = this.totalItems;
				}
			}
		});
	}

	addCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceCategoryId": 0,
			"genMaintenanceCategoryName": data.genMaintenanceCategoryName,
			"description": data.description,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": 0,
			"updatedOn": new Date().toISOString,
		}

		let params: any = {};
		params.model = reqObj;
		this.genMaintService.addGenMaintenanceCategory(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage("Category added successfully");
				this.isAssetLoaded = false;
				this.row = {};
				this.getAllCategory();
			}

		});
	}

	updateCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceCategoryId": data.genMaintenanceCategoryId,
			"genMaintenanceCategoryName": data.genMaintenanceCategoryName,
			"description": data.description,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.model = reqObj;
		this.genMaintService.updateGenMaintenanceCategory(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage("Category updated successfully");
				this.isAssetLoaded = false;
				this.opIndex = -1;
			}

		});
	}


	getAllSubCategory() {
		this.isAssetLoaded = true;
		this.genMaintService.getAllGenMaintenanceSubCategory().subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				this.subCategoriesData = res ? res : [];
				this.tempSubCategoriesData = res ? res : [];
				this.subTotalItems = this.subCategoriesData.length;
				if (this.subTotalItems > this.subItemLimit) {
					this.subItemEndIndex = this.subItemLimit;
				}
				else {
					this.subItemEndIndex = this.subTotalItems;
				}
			}
		});
	}

	addSubCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceSubCategoryId": 0,
			"genMaintenanceCategoryId": data.genMaintenanceCategoryId,
			"genMaintenanceSubCategoryName": data.genMaintenanceSubCategoryName,
			"description": data.description,
			"frequency": data.frequency,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": 0,
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.model = reqObj;
		this.genMaintService.addGenMaintenanceSubCategory(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage(" Sub Category added successfully");
				this.isAssetLoaded = false;
				this.subCatgory = {};
				this.getAllSubCategory();
			}

		});
	}

	updateSubCategory(data) {
		let reqObj: any = {};
		reqObj = {
			"genMaintenanceSubCategoryId": data.genMaintenanceSubCategoryId,
			"genMaintenanceCategoryId": data.genMaintenanceCategoryId,
			"genMaintenanceSubCategoryName": data.genMaintenanceSubCategoryName,
			"description": data.description,
			"frequency": data.frequency,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.model = reqObj;
		this.genMaintService.updateGenMaintenanceSubCategory(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage("Sub Category updated successfully");
				this.isAssetLoaded = false;
				this.opSubIndex = -1;
				this.getAllSubCategory();
			}

		});
	}
}
