import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { FormBuilder, FormArray, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-initial-maintanance',
  templateUrl: './initial-maintanance.component.html',
  styleUrls: ['./initial-maintanance.component.scss']
})
export class InitialMaintananceComponent implements OnInit {

  search:any ={};
	assetList:any = [];
	categoryList :any = [];
	subCategoryList:any = [];
	selectedItems:any =[];
    unitFieldType:string = "unitno";
	  unitOrder:boolean = false;
	  externalListData:any;
	  externalData:string = "";
	  isExternalDataLoaded:boolean = false;
	  externalDeleteId:string = "";
	  errorMessage:string = "";
	  isError:boolean = false; 
	  userTable: FormGroup;
	  control: FormArray;
	  mode: boolean;
	  touchedRows: any;
  


  constructor(
	private router: Router,
	private cookieService:CookieService,
	public dialog: MatDialog,
		private fb: FormBuilder
  ) { 

	// this.modalService = this.injector.get(ModalService);
  }

  	ngOnInit(): void {
	  this.isExternalDataLoaded = true;
  
	  this.touchedRows = [];
	  this.userTable = this.fb.group({
		tableRows: this.fb.array([])
	  });
	  this.addRow();
	}
  
	ngAfterViewInit() {
	  this.control = this.userTable.get('tableRows') as FormArray;
	}
  
	sortUnitData(type){
	  this.unitFieldType = type;
	  this.unitOrder = !this.unitOrder;
	}
  
	getFieldOrderBy(type) {
	  if(this.unitFieldType == type){
		return this.unitOrder ? 'desc' : 'asc';
	  }
	  else return '';
	}
  
	isMobileView() {
	  return window.innerWidth <= 767 ? 'table-responsive' : '';
	}
  
	initiateForm(): FormGroup {
	  return this.fb.group({
		serviceType: ['', Validators.required],
		email: ['', [Validators.email, Validators.required]],
		contactperson: ['', Validators.required],
		phone: ['', [Validators.required, Validators.maxLength(10)]],
		isEditable: [true]
	  });
	}
  
  
	addRow() {
	  const control =  this.userTable.get('tableRows') as FormArray;
	  control.push(this.initiateForm());
	}
  
	deleteRow(index: number) {
	  const control =  this.userTable.get('tableRows') as FormArray;
	  control.removeAt(index);
	}
  
	editRow(group: FormGroup) {
	  group.get('isEditable').setValue(true);
	}
  
	doneRow(group: FormGroup) {
	  group.get('isEditable').setValue(false);
	}
  
	saveUserDetails() {
	  console.log(this.userTable.value);
	}
  
	get getFormControls() {
	  const control = this.userTable.get('tableRows') as FormArray;
	  return control;
	}
  
	submitForm() {
	  const control = this.userTable.get('tableRows') as FormArray;
	  this.touchedRows = control.controls.filter(row => row.touched).map(row => row.value);
	  console.log(this.touchedRows);
	}

}
